import { HttpClient, HttpHeaders } from '@angular/common/http';

import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { environment } from 'src/environments/environment.prod';
const headers = new HttpHeaders({ 'Content-Type': 'application/json', 'strAppinfo': 'big' })
@Component({
  selector: 'app-otp-change-password',
  templateUrl: './otp-change-password.component.html',
  styleUrls: ['./otp-change-password.component.scss']
})
export class OtpChangePasswordComponent implements OnInit {
error=false;
  first:boolean=true;
  second:boolean=false;
  third:boolean=false;
  newPassword:FormGroup;
  enterMobile:FormGroup;
  otpForm:FormGroup;
  session: any;
  constructor( private fb: FormBuilder,private http:HttpClient) {this.createForm(),this.mobileForm(),this.otp() }

  ngOnInit(): void {
  }
  toSecond(){
    this.second=true;
    this.first=false;
  }
  toThird(){
    this.second=false;
    this.third=true;
  }
  createForm() {
    this.newPassword = this.fb.group({
      
      password:['',Validators.required],
      cpassword:['',Validators.required]
      
    });

}
get k() { return this.newPassword.controls; }
otp() {
  this.otpForm = this.fb.group({
    
    strotp:['',Validators.required],
   
    
  });

}
get m() { return this.otpForm.controls; }
mobileForm(){
  this.enterMobile = this.fb.group({
      
    mobile:['',Validators.required],
   
    
  });

}
get f() { return this.enterMobile.controls; }

inputInsideOtpInput(el) {
  console.log(el);
  
  if (el.value.length > 1){
      el.value = el.value[el.value.length - 1];
  }
  try {
      if(el.value == null || el.value == ""){
          this.foucusOnInput(el.previousElementSibling);
      }else {
          this.foucusOnInput(el.nextElementSibling);
      }
  }catch (e) {
      console.log(e);
  }
}

foucusOnInput(ele){
 ele.focus();
 let val = ele.value;
 ele.value = "";
 // ele.value = val;
 setTimeout(()=>{
     ele.value = val;
 })
}
otpSubmit(){
//   let param={ 
//     "strStep":"FORGOT",
//  "strName": this.f.mobile.value
//  }
//  return this.http.post(environment.apiUrl+'user/forgot_password',param,{headers:headers}).subscribe((body)=>{
//    console.log(body);
//    this.session=body['Details']
//  })
}
onChange(){
  console.log(this.m.strotp.value);
  console.log(this.k.password.value);
  console.log(this.k.cpassword.value);
  
  
if(this.k.password.value==this.k.cpassword.value){
  let param={
    "strStep":"VERIFY",
    "strSessionId":this.session,
 "strName": this.f.mobile.value,
 "strPassword":this.k.password.value,
 "strOTP":this.m.strotp.value
  }
  console.log(param);
  return this.http.post(environment.apiUrl+'user/forgot_password',param,{headers:headers}).subscribe((body)=>{
    console.log(body);
    
  })
}
else{
  this.error=true;
}
}
}
