import { FormGroup, FormBuilder } from '@angular/forms';
import { Component, OnInit, Inject } from '@angular/core';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';
import { HttpHeaders, HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
const token = localStorage.getItem('token');
@Component({
  selector: 'app-edit-category',
  templateUrl: './edit-category.component.html',
  styleUrls: ['./edit-category.component.scss']
})
export class EditCategoryComponent implements OnInit {
  imageChange=false;
  urlCategory:any;
  image;
  editCategory:FormGroup
  constructor(  @Inject(MAT_DIALOG_DATA) public data: any,private fb:FormBuilder,private http:HttpClient,private route:Router) {this.form() }

  ngOnInit(): void {
    console.log(this.data.res)
    this.urlCategory=this.data.res.strImgUrl_0;
    this.formload()
  
  }
  form(){
    this.editCategory=this.fb.group({
      strName:[''],
      images:['']
    })
  }
  formload(){
    this.editCategory.patchValue({
      strName: this.data.res.strName,
      
    });
    // this.editBrand.get('avatar').updateValueAndValidity()
  }
  onSelectCategory(event) {
    this.imageChange==true;
    const file = (event.target as HTMLInputElement).files[0];
    if (event.target.files && event.target.files[0]) {
      var reader = new FileReader();
  
      reader.readAsDataURL(event.target.files[0]); // read file as data url
  
      reader.onload = (event) => { // called once readAsDataURL is completed
        this.urlCategory = event.target.result;
      }
    }
    this.editCategory.patchValue({
      images: file
    });
    this.editCategory.get('images').updateValueAndValidity()
    

  }
  onSubmit(){
    if(this.imageChange==true){
      let headers = new HttpHeaders({  'Authorization': token, 'strAppInfo': 'TNT1' })
      const formData = new FormData()
      formData.append('images',this.editCategory.get('images').value)
      return this.http.post('http://15.207.68.165:4001/file/files_upload',formData,{headers}).subscribe((body)=>{
        console.log(body)
        this.image=body["arrImageUrl"]
        this.afterSubmit();
        
      
  
      },
      (error)=>{
        if(error){
          console.log(error)
        }
      })
  
    }
    else{
      const headers = new HttpHeaders({ 'Content-Type': 'application/json', 'Authorization': token, 'strAppInfo': 'TNT1' })
      let options={
        "strOperationType":"UPDATE",
        "strDocId":this.data.res._id,
        "strName":this.editCategory.get('strName').value,
        "strCollection":"cln_category",
        "strImgUrl_0": this.urlCategory
      }
      return this.http.post('http://15.207.68.165:4001/master/create_master',options,{headers}).subscribe((body)=>{
        console.log(body)
        window.location.reload()
      },
      (error)=>{
        if(error){
          console.log(error)
        }
      })

    }

  }
  afterSubmit(){
    const headers = new HttpHeaders({ 'Content-Type': 'application/json', 'Authorization': token, 'strAppInfo': 'TNT1' })
    let options={
      "strOperationType":"UPDATE",
      "strDocId":this.data.res._id,
      "strName":this.editCategory.get('strName').value,
      "strCollection":"cln_brand",
      "strImgUrl_0":this.image
    }
    return this.http.post('http://15.207.68.165:4001/master/create_master',options,{headers}).subscribe((body)=>{
      console.log(body)
      window.location.reload()
    },
    (error)=>{
      if(error){
        console.log(error)
      }
    })
  }

}
