import { DeleteCustomerComponent } from './../dialog/delete-customer/delete-customer.component';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatTableDataSource } from '@angular/material/table';
import { MatSort } from '@angular/material/sort';
import { MatDialog } from '@angular/material/dialog';
interface IcustomerList{
  strMobileNo:any;
  strAddress:any;
}
@Component({
  selector: 'app-customers',
  templateUrl: './customers.component.html',
  styleUrls: ['./customers.component.scss']
})
export class CustomersComponent implements OnInit {
  
  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  public   list:any;
  displayedColumns: string[] = ['position','strName','strMobileNo','strAddress','action'];
  dataSource =new MatTableDataSource<IcustomerList>(this.list);
  @ViewChild(MatSort, {static: true}) sort: MatSort;
  productLength=this.dataSource.data.length;
  public errors=[]
  constructor(private http:HttpClient,public dialog: MatDialog) { }

  ngOnInit(): void {
    this.getCustomer()
  }
  pageNumber=0;
  pageRecord=10
  private master:any={
    "intLimit":10,
  "intPageNo":0,
   "strType":"USER",
   "strSearch":null

  }
getCustomer(){
  const token = localStorage.getItem('token');




const header = new HttpHeaders({  'Authorization': token })
 
  return this.http.post('http://15.207.68.165:4001/user/get_user',this.master,{headers:header}).subscribe((list)=>{
    console.log(list);
    this.list=list["arrList"]
   
    this.dataSource = new MatTableDataSource<IcustomerList>(this.list);
    this.dataSource.sort = this.sort;
  })
}
valueLimit(e){
  console.log(e)
  this.master.intLimit=e
  this.getCustomer()
}
search(p){
  this.master.strSearch=p
  this.getCustomer()
}
pageInc(){
  this.pageNumber=this.pageNumber+1;
  this.master.intPageNo=this.pageNumber
  this.getCustomer()
  var btn = document.getElementById("btnCounter");
  if(this.pageNumber>0){
    btn.removeAttribute('disabled')
  }
  console.log(this.pageNumber);
  
      
     }
     pageDec(){
       if(this.pageNumber)
       this.pageNumber=this.pageNumber-1;
       this.master.intPageNo=this.pageNumber
       this.getCustomer()
       var btn = document.getElementById("btnCounter");
     
       console.log(this.pageNumber);
       
     }
     openDialogDelete(e) {
      const dialogRef = this.dialog.open(DeleteCustomerComponent,{
        data: {
          dataKey: e
        }
      });
    
      dialogRef.afterClosed().subscribe(result => {
        console.log(`Dialog result: ${result}`);
        this.getCustomer()
      });
    }
    whatsappmsg(id){
      window.open('https://api.whatsapp.com/send?phone='+id)
    }
}
