  private brandData:any={
    "arrCollection":[
      {"strCollection":"cln_brand"},
     
     ],
     "strSearch":null
  }
  valueBrand(a){
    this.brandData.strSearch=a
    this.loadBrand()
  }
  loadBrand(){
    return this.http.post(environment.apiUrl+'master/get_master',this.brandData,{headers}).subscribe((brand)=>{
      this.brand=brand['cln_brand']
    })
  }
  valueCategory(b){
    this.CategoryData.strSearch=b
    this.loadCategory()
  }
  private CategoryData:any={
    "arrCollection":[
      {"strCollection":"cln_category"},
     
     ],
     "strSearch":null
  }
  loadCategory(){
    return this.http.post(environment.apiUrl+'master/get_master',this.CategoryData,{headers}).subscribe((cat)=>{
      this.category=cat['cln_category']
    })
  }
