import { Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { HttpHeaders, HttpClient } from '@angular/common/http';
const token = localStorage.getItem('token');


const headers = new HttpHeaders({ 'Content-Type': 'application/json', 'Authorization': token, 'strAppInfo': 'TNT1' })
@Component({
  selector: 'app-add-brand',
  templateUrl: './add-brand.component.html',
  styleUrls: ['./add-brand.component.scss']
})
export class AddBrandComponent implements OnInit {
  addBrand:FormGroup;
  image;
  strName;
  images;
  urlBrand:any;
  constructor(private fb:FormBuilder,private http:HttpClient,private Router:Router) {this.form() }

  ngOnInit(): void {
  }
  form(){
    this.addBrand=this.fb.group({
      strName:['',Validators.required],
      images:[null]
    })
    }
  onSelectBrand(event) {
    const file = (event.target as HTMLInputElement).files[0];
    if (event.target.files && event.target.files[0]) {
      var reader = new FileReader();
  
      reader.readAsDataURL(event.target.files[0]); // read file as data url
  
      reader.onload = (event) => { // called once readAsDataURL is completed
        this.urlBrand = event.target.result;
      }
    }
    this.addBrand.patchValue({
      images: file
    });
    this.addBrand.get('images').updateValueAndValidity()
  }
  onSubmit(){
    let headers = new HttpHeaders({  'Authorization': token, 'strAppInfo': 'TNT1' })
    const formData = new FormData()
    formData.append('images',this.addBrand.get('images').value)
    return this.http.post('http://15.207.68.165:4001/file/files_upload',formData,{headers}).subscribe((body)=>{
      console.log(body)
      this.image=body["arrImageUrl"]
      this.afterSubmit();
      
    

    },
    (error)=>{
      if(error){
        console.log(error)
      }
    })


  }
  afterSubmit(){
    let param={
       
      strCollection:"cln_brand",
      strName:this.addBrand.get('strName').value,
      strImgUrl_0:this.image

    }
    return this.http.post('http://15.207.68.165:4001/master/create_master',param,{headers}).subscribe((body)=>{
      console.log(body)
      // window.location.reload()
    },
    (error)=>{
      if(error){
        console.log(error)
      }
    })
    
  }

}
