import { environment } from './../../environments/environment.prod';
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, FormControl } from '@angular/forms';
import { HttpHeaders, HttpClient } from '@angular/common/http';
import { MatSnackBar } from '@angular/material/snack-bar';
import { empty } from 'rxjs';

const token = localStorage.getItem('token');
const id=localStorage.getItem('id')
@Component({
  selector: 'app-shop-details',
  templateUrl: './shop-details.component.html',
  styleUrls: ['./shop-details.component.scss']
})
export class ShopDetailsComponent implements OnInit {
shopForm:FormGroup;

selectedValue:any;
wallCoverUrl:any;
logoUrl:any;
  wallImage: any;
  logoImage: any;
  delivery;
  shop: any;
  strSettingId:string;
  cart: any;
  product: any;

  constructor(private fb:FormBuilder,private http:HttpClient,private _snackBar: MatSnackBar,) {this.shopDetail() }

  ngOnInit(): void {
    this.wallCoverUrl="assets/puma.jpg";
    this.logoUrl="assets/puma.jpg";
    this.getDeliveryOption()
    this.getModule()
  }
  shopDetail(){
    this.shopForm=this.fb.group({
      blnLogoShow:[''],
      blnAboutShow:[''],
      blnCoverImageShow:[''],
      strLogoUrl:[''],
      strCoverImage:[''],
      strBGColor:[''],
      strTittle:[''],
      blnTextItalic:[''],
      blnTextUnderline:[''],
      blnTextBold:[''],
      
      strAbout:[''],
      strSortOrder: [''],
      strSortBy: [''],
      blnPriceShow: [''],
      blnQualityShow: [''],
      strOrderMessage:[''],
      strDelivery:[''],
      dblMobile:[''],
      blnOrderChargeShow:[''],
      blnExtraChargeShow:['']

    })
  }

  get f() { return this.shopForm.controls; }
  update(){
    console.log(this.shopForm.value);
    
  }
  onSelectWall(event) {
    const file = (event.target as HTMLInputElement).files[0];
    if (event.target.files && event.target.files[0]) {
      var reader = new FileReader();

      reader.readAsDataURL(event.target.files[0]); // read file as data url

      reader.onload = (event) => { // called once readAsDataURL is completed
        this.wallCoverUrl = event.target.result;
      }
    }
    this.shopForm.patchValue({
      strCoverImage: file
    });
    this.shopForm.get('strCoverImage').updateValueAndValidity()
    let headers = new HttpHeaders({  'Authorization': token, 'strAppInfo': 'TNT1' })
    const formData = new FormData()
      formData.append('images',this.shopForm.get('strCoverImage').value)
      return this.http.post('http://15.207.68.165:4001/file/files_upload',formData,{headers}).subscribe((body)=>{
        console.log(body)
        this.wallImage=body["arrImageUrl"]
      })
  }
  
  onSelectCover(event) {
    const files = (event.target as HTMLInputElement).files[0];
    if (event.target.files && event.target.files[0]) {
      var reader = new FileReader();

      reader.readAsDataURL(event.target.files[0]); // read file as data url

      reader.onload = (event) => { // called once readAsDataURL is completed
        this.logoUrl = event.target.result;
      }
    }
    this.shopForm.patchValue({
      strLogoUrl: files
    });
    this.shopForm.get('strLogoUrl').updateValueAndValidity()
    let headers = new HttpHeaders({  'Authorization': token })
    const formData = new FormData()
      formData.append('images',this.shopForm.get('strLogoUrl').value)
      return this.http.post('http://15.207.68.165:4001/file/files_upload',formData,{headers}).subscribe((body)=>{
        console.log(body)
        this.logoImage=body["arrImageUrl"]
      })
  }

  onSumbitShop(){
    const headers = new HttpHeaders({  'Authorization': localStorage.getItem('token')})
    let param={
      "_id": this.strSettingId,
      "strOperationType":"UPDATE",
      "objSettings":{
      "strTenantId": "big",
      "strName": "mdl_home",
      "strTheame": "BASIC",
      "chrStatus": "N",
      "objShop": {
        "blnLogoShow": this.f.blnLogoShow.value,
        "blnCoverImageShow": this.f.blnCoverImageShow.value,
        "strLogoUrl": this.logoImage,
        "strCoverImage": this.wallImage,
        "strBGColor": this.f.strBGColor.value,
        "blnAboutShow": this.f.blnAboutShow.value,
        "strAbout": this.f.strAbout.value,
        "strTittle": this.f.strTittle.value,
        "blnTextItalic": true,
        "blnTextUnderline": true,
        "blnTextBold": true,
        "dblMobile":this.f.dblMobile.value
    },
    "objProduct":this.product,
"objCart":this.cart
      }
    }
    return this.http.post('http://15.207.68.165:4001/common/create_settings',param,{headers:headers}).subscribe((cart)=>{
      console.log(cart);
      this._snackBar.open('You have  successfully updated', '', {
        duration: 2000,
      });

    })
  }

  onSubmitProduct(){
    const headers = new HttpHeaders({  'Authorization': token})
    let param={
      "_id": id,
      "strOperationType":"UPDATE",
      "objSettings":{
      "strTenantId": "big",
      "strName": "mdl_home",
      "strTheame": "BASIC",
      "chrStatus": "N",
      "objProduct": {
        "strSortOrder": this.f.strSortOrder.value,
        "strSortBy": this.f.strSortBy.value,
        "blnPriceShow": this.f.blnPriceShow.value,
        "blnQualityShow": this.f.blnQualityShow.value
    },
    "objshop":this.shop,
    "objCart":this.cart
      }
    }
    return this.http.post('http://15.207.68.165:4001/common/create_settings',param,{headers:headers}).subscribe((cart)=>{
      console.log(cart);
      this._snackBar.open('You have  successfully updated', '', {
        duration: 2000,
      });
    })
  }

  onSubmitCart(){
    const headers = new HttpHeaders({  'Authorization': token})
    let param={
      "_id": id,
      "strOperationType":"UPDATE",
      "objSettings":{
      "strTenantId": "big",
      "strName": "mdl_home",
      "strTheame": "BASIC",
      "chrStatus": "N",
      "objCart":{
        'blnExtraChargeShow': this.f.blnExtraChargeShow.value,
        'blnOrderChargeShow': this.f.blnOrderChargeShow.value,
        'strOrderMessage': this.f.strOrderMessage.value
      },
      "objshop":this.shop,
    "objProduct":this.product
      }
    }
    return this.http.post('http://15.207.68.165:4001/common/create_settings',param,{headers:headers}).subscribe((cart)=>{
      console.log(cart);
      this._snackBar.open('You have  successfully updated', '', {
        duration: 2000,
      });

    })
  
    
  }
  addDeliveryoption(){
    if(this.f.strDelivery.value == ''){
      this._snackBar.open('Invalid Delivery Option', '', {
        duration: 2000,
      });
    }
    else{
      const headers = new HttpHeaders({  'Authorization': token})
      let param={
        "strName":this.f.strDelivery.value,
        "strCollection":"cln_delivery_option",
       
      }
      return this.http.post('http://15.207.68.165:4001/master/create_master',param,{headers:headers}).subscribe((body)=>{
        console.log(body);
        this._snackBar.open('You have  successfully saved', '', {
          duration: 2000,
          
        });
        this.getDeliveryOption()
      })
    }
  }
  getDeliveryOption(){
    const headers = new HttpHeaders({  'Authorization': token})
    let param={
      "arrCollection":[
       {"strCollection":"cln_delivery_option"},
       
      ]

}
return this.http.post('http://15.207.68.165:4000/master/get_master',param,{headers:headers}).subscribe((body)=>{
  this.delivery=body['cln_delivery_option']
  console.log(this.delivery);
  
})
  }
  deleteDelivery(id){
    console.log(id);
    
    const headers = new HttpHeaders({  'Authorization': token})
    let param={
      "arrDeleteId":[id],
      "strCollection":"cln_delivery_option"
    }
    return this.http.post('http://15.207.68.165:4001/master/delete_master',param,{headers:headers}).subscribe((body)=>{
      console.log(body);
      this._snackBar.open('You have  successfully deleted', '', {
        duration: 2000,
      });
      this.getDeliveryOption()
    })
   }
  getModule(){

const head = new HttpHeaders({ 'Content-Type': 'application/json','strAppInfo': 'big' ,'Authorization':token})
    let param={"strName":"mdl_home"}
    return this.http.post(environment.apiUrl+'common/get_module_settings',param,{headers:head}).subscribe((theme)=>{
      this.strSettingId=theme["_id"]
      this.shop=theme['objShop']
      this.cart=theme['objCart']
      this.product=theme['objProduct']
      console.log(this.cart);
      this.shopForm.patchValue({
        blnLogoShow:this.shop.blnLogoShow,
       
        blnAboutShow:this.shop.blnAboutShow,
        blnCoverImageShow:this.shop.strCoverImage,
      
        strLogoUrl:this.shop.strLogoUrl,
        strCoverImage:this.shop.strCoverImage,
        strBGColor:this.shop.strBGColor,
        strTittle:this.shop.strTittle,
        blnTextItalic:this.shop.blnTextItalic,
        blnTextUnderline:this.shop.blnTextUnderline,
        blnTextBold:this.shop.blnTextBold,
        
        strAbout:this.shop.strAbout,
        strSortOrder: this.product.strSortOrder,
        strSortBy: this.product.strSortBy,
        blnPriceShow: this.product.blnPriceShow,
        blnQualityShow:this.product.blnQualityShow,
        strmsg:[''],
        strDelivery:[''],
        dblMobile:this.shop.strMobileNumber,
        strOrderMessage:this.cart.strOrderMessage,
        blnOrderChargeShow:this.cart.blnOrderChargeShow,
      blnExtraChargeShow:this.cart.blnExtraChargeShow

      })
      this.logoImage=this.shop.strLogoUrl,
       this.wallImage=this.shop.strCoverImage,
      this.wallCoverUrl=this.shop.strCoverImage,
      this.logoUrl=this.shop.strLogoUrl
      
    })
  }
  themeApply(){
    this._snackBar.open('You have  successfully applied', '', {
      duration: 2000,
    });

  }
}
