import { ResThemeHomeComponent } from './themes/restaurent/res-theme-home/res-theme-home.component';
import { EditProductComponent } from './edit-product/edit-product.component';

import { LoginComponent } from './login/login.component';
import { AddProductComponent } from './add-product/add-product.component';
import { MyShopComponent } from './my-shop/my-shop.component';
import { ShopDetailsComponent } from './shop-details/shop-details.component';
import { CustomersComponent } from './customers/customers.component';
import { CategoriesComponent } from './categories/categories.component';
import { AccountSettingsComponent } from './account-settings/account-settings.component';
import { OrderComponent } from './order/order.component';
import { HomeComponent } from './home/home.component';
import { ProductComponent } from './product/product.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
// import { DefaultThemeComponent } from './themes/default-theme/default-theme.component';
import { AuthGuard } from './_helpers/auth.guard';

const routes: Routes = [
  {
    path:'',redirectTo:'login',pathMatch:'full'
  },
  {
    path:'login',component:LoginComponent,
  },

// {
//   path:'myshop',component:ResThemeHomeComponent
// },
{
  path:'home',component:HomeComponent,canActivate :[AuthGuard],children:[
    {
    path:'product',component:ProductComponent
  },
  {
    path:'dashboard',component:DashboardComponent
  },
  {
  path:'order',component:OrderComponent
  },
  {
    path:'settings',component:AccountSettingsComponent
  },
  {
    path:'categories',component:CategoriesComponent
  },
  {
    path:'customers',component:CustomersComponent
  },
  {
    path:'shopdetail',component:ShopDetailsComponent
  },
  // {
  //   path:'myshop',component:MyShopComponent,
  // },
  {
    path:'product/addnew',component:AddProductComponent
  },
  {
    path:'product/edit/:id',component:EditProductComponent
  }

  ]
},
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
