import { HttpClient, HttpHeaders } from '@angular/common/http';

import { ResThemeProductDetailComponent } from './../res-theme-product-detail/res-theme-product-detail.component';
import { ResThemeCartComponent } from './../res-theme-cart/res-theme-cart.component';
import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { environment } from 'src/environments/environment.prod';
const token = localStorage.getItem('token');
const head = new HttpHeaders({ 'Content-Type': 'application/json', 'Authorization': token ,'strAppInfo': 'big' })
@Component({
  selector: 'app-res-theme-home',
  templateUrl: './res-theme-home.component.html',
  styleUrls: ['./res-theme-home.component.scss']
})
export class ResThemeHomeComponent implements OnInit {
  @ViewChild('widgetsContent') widgetsContent: ElementRef;
  shop: any;
  constructor(public dialog: MatDialog,private http:HttpClient) { }

  ngOnInit(): void {
    this.getTheme()
  }
  getTheme(){
    let param={
      "strName":"mdl_home"
    }
    return this.http.post(environment.apiUrl+'common/get_module_settings',param,{headers:head}).subscribe((body)=>{
      console.log(body)
      this.shop=body['objShop']
      console.log(this.shop);
      
    })
  }
  openCart() {
    const dialogRef = this.dialog.open(ResThemeCartComponent,{
      width:'90%',
      height:'90%'
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log(`Dialog result: ${result}`);
    });
  }
  openProduct() {
    const dialogRef = this.dialog.open(ResThemeProductDetailComponent,{
      width:'90%',
      height:'90%'
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log(`Dialog result: ${result}`);
    });
  }
  scrollLeft(){
    this.widgetsContent.nativeElement.scrollLeft -= 150;
  }

  scrollRight(){
    this.widgetsContent.nativeElement.scrollLeft += 150;
  }
  createCart(id){
    let headers = new HttpHeaders({ 'strAppInfo': 'big' })
    let param={
      "strProductId":id,
          "dblQty":1
    }
    return this.http.post('http://15.207.68.165:4000/',param,{headers:headers}).subscribe((cartProduct)=>{
      console.log(cartProduct);
      
    })
  }
}
