import { Router } from '@angular/router';
import { environment } from './../../../environments/environment.prod';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
const token = localStorage.getItem('token');


const headers = new HttpHeaders({ 'Content-Type': 'application/json', 'Authorization': token, 'strAppInfo': 'TNT1' })
@Component({
  selector: 'app-add-category',
  templateUrl: './add-category.component.html',
  styleUrls: ['./add-category.component.scss']
})
export class AddCategoryComponent implements OnInit {
  addCategory:FormGroup;
  image;
  strName;
  images;
  urlCategory:any;
  constructor(private http:HttpClient,private fb:FormBuilder,private router:Router) { this.form()}

  ngOnInit(): void {
  console.log(token)
    
  }
form(){
this.addCategory=this.fb.group({
  strName:['',Validators.required],
  images:[null]
})
}
 
  onSelectCategory(event) {
    const file = (event.target as HTMLInputElement).files[0];
    if (event.target.files && event.target.files[0]) {
      var reader = new FileReader();
  
      reader.readAsDataURL(event.target.files[0]); // read file as data url
  
      reader.onload = (event) => { // called once readAsDataURL is completed
        this.urlCategory = event.target.result;
      }
    }

    this.addCategory.patchValue({
      images: file
    });
    this.addCategory.get('images').updateValueAndValidity()
  }
  // formData.append("name", this.form.get('name').value);
  // formData.append("avatar", this.form.get('avatar').value);
  onSubmit(){
    let headers = new HttpHeaders({  'Authorization': token, 'strAppInfo': 'TNT1' })
    const formData = new FormData()
    formData.append('images',this.addCategory.get('images').value)
    return this.http.post('http://15.207.68.165:4001/file/files_upload',formData,{headers}).subscribe((body)=>{
      console.log(body)
      this.image=body["arrImageUrl"]
      this.afterSubmit();
    
    

    },
    (error)=>{
      if(error){
        console.log(error)
      }
    })


  }
  afterSubmit(){
    let param={
       
      strCollection:"cln_category",
      strName:this.addCategory.get('strName').value,
      strImgUrl_0:this.image

    }
    return this.http.post('http://15.207.68.165:4001/master/create_master',param,{headers}).subscribe((body)=>{
      console.log(body)
      window.location.reload()
    },
    (error)=>{
      if(error){
        console.log(error)
      }
    })
  }
}
