import { DeleteProductComponent } from './../dialog/delete-product/delete-product.component';
import { environment } from './../../environments/environment.prod';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Component, OnInit, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ViewEncapsulation } from '@angular/compiler/src/core';
import { MatTableDataSource } from '@angular/material/table';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatDialog } from '@angular/material/dialog';
import { Router } from '@angular/router';
const token = localStorage.getItem('token');
const headers = new HttpHeaders({ 'Content-Type': 'application/json', 'Authorization': token })
interface IproductList{
  strImageUrl:string,
  strSKUId:string,
  strName:string,
  strCategory:string,
  dblMRP:any,
  strStock:string,
  intPosition:any,
  dblSellingPrice:any;
}
@Component({
  selector: 'app-product',
  templateUrl: './product.component.html',
  styleUrls: ['./product.component.scss'],

})
export class ProductComponent implements OnInit {
  // @ViewChild('scheduledOrdersPaginator',) paginator: MatPaginator;
  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  public   productList:any;
  displayedColumns: string[] = ['position', 'strImageUrl', 'strSKUId', 'strName','strCategory','dblMRP','strStock','intPosition','action'];
  

  dataSource =new MatTableDataSource<IproductList>(this.productList);
  pageNumber=0;
  pageRecord=10
  private master: any = {
    "intLimit":10,
  "intPageNo":0,
      "strSort":"strName",
      "strSortActive":"ASC",
      "arrCategory":[],
      "arrBrands":[],
      "strSearch":null
  }
  
  @ViewChild(MatSort, {static: true}) sort: MatSort;
  productLength=this.dataSource.data.length;
  public errors=[]
 
 
  constructor(private fb:FormBuilder
    ,private http:HttpClient,
    public dialog: MatDialog,private router: Router,) {
    
   }

  
  ngOnInit(): void {
    this.getProduct()
    console.log(this.master.intLimit)
    //this.dataSource.sort = this.sort;
 
  }
  ngAfterViewInit(): void {
    this.dataSource.sort = this.sort;
  
  }
  pageEvent(e){
    
  }
 
  pageInc(){
  this.pageNumber=this.pageNumber+1;
  this.master.intPageNo=this.pageNumber
  this.getProduct()
  var btn = document.getElementById("btnCounter");
  if(this.pageNumber>0){
    btn.removeAttribute('disabled')
  }
  console.log(this.pageNumber);
  
      
     }
     pageDec(){
       if(this.pageNumber)
       this.pageNumber=this.pageNumber-1;
       this.master.intPageNo=this.pageNumber
       this.getProduct()
       var btn = document.getElementById("btnCounter");
     
       console.log(this.pageNumber);
       
     }
     valuechangeCat(e){
       console.log(e)
       this.master.arrCategory=[e]
     }
     valueLimit(e){
      this.master.intLimit=e
      console.log(this.master.intLimit)
      this.getProduct()
    }
     valuechangeBrand(p){
       console.log(p)
       this.master.arrBrands=p
     }
     stopPropagation(event){
      event.stopPropagation();
  }
  
getProduct(){
  console.log(this.master);
  
  // let param={"intLimit":10,
  // "intPageNo":0,
  //     "strSort":"strName",
  //     "strSortActive":"ASC",
  //     "arrCategory":[],
  //     "arrBrands":[]
  // }
  this.http.post(environment.apiUrl+'product/get_product',this.master,{headers}).subscribe((body)=>{
    this.productList=body['arrList'];
    this.dataSource = new MatTableDataSource<IproductList>(this.productList);
    this.dataSource.sort = this.sort;
    console.log(this.dataSource)
  })
}

clearFilter(){
  this.master.arrCategory=null
  this.getProduct()
}
applyFilter(event: Event) {
  const filterValue = (event.target as HTMLInputElement).value;
  this.dataSource.filter = filterValue.trim().toLowerCase();
}
search(e){
  this.master.strSearch=e
  this.getProduct()
}
openDialogDelete(e) {
  const dialogRef = this.dialog.open(DeleteProductComponent,{
    data: {
      dataKey: e
    }
  });

  dialogRef.afterClosed().subscribe(result => {
    console.log(`Dialog result: ${result}`);
  });
}
addnewlink(){

this.router.navigate(['/home/product/addnew']);
}

}
