import { Router } from '@angular/router';
import { User } from './../_models/user';
import { environment } from './../../environments/environment.prod';

import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { BehaviorSubject, Observable } from 'rxjs';
import { map } from 'rxjs/operators';
// import { JwtHelperService } from '@auth0/angular-jwt';

const headers = new HttpHeaders({ 'strAppInfo': 'big' })
@Injectable({ providedIn: 'root' })
export class AuthenticationService {
    private loggedInSubject: BehaviorSubject<User>;
    public loggedIn: Observable<User>;

    constructor(private http: HttpClient,private route:Router,) {
        this.loggedInSubject = new BehaviorSubject<User>(JSON.parse(localStorage.getItem('loggedIn')));
        this.loggedIn = this.loggedInSubject.asObservable();
    }
    // public isAuthenticated(): boolean {
    //     const token = localStorage.getItem('token');
    //     // Check whether the token is expired and return
    //     // true or false
    //     return !this.jwtHelper.isTokenExpired(token);
    //   }

    public get loggedInValue(): User {
        return this.loggedInSubject.value;
    }

    login(strName: string, strPassword: string) {
        return this.http.post<any>(`${environment.apiUrl}user/login_user`, { strName, strPassword },{headers})
            .pipe(map(user => {
                // store user details and jwt token in local storage to keep user logged in between page refreshes
                localStorage.setItem('loggedIn', JSON.stringify(user));
                this.loggedInSubject.next(user);
                return user;
                
            }));
            
    }

    logout() {
        // remove user from local storage to log user out
        localStorage.removeItem('loggedIn');
        this.loggedInSubject.next(null);
        this.route.navigateByUrl('/login')
    }
}