import { DeleteOrderComponent } from './../dialog/delete-order/delete-order.component';
import { StatusUpdateComponent } from './../dialog/status-update/status-update.component';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatTableDataSource } from '@angular/material/table';
import { MatSort } from '@angular/material/sort';
import { FormBuilder } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
const token = localStorage.getItem('token');
interface IorderList{
  strOrderId:string,
  strCreatedTime:any,
  strName:any,
  strModeDelivery:string,
  strAdress:any,
  strOrderStatus:string,
 
}
@Component({
  selector: 'app-order',
  templateUrl: './order.component.html',
  styleUrls: ['./order.component.scss']
})
export class OrderComponent implements OnInit {

  constructor(private fb:FormBuilder
    ,private http:HttpClient,
    public dialog: MatDialog) { }
  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  public   orderList:any;
  displayedColumns: string[] = ['strOrderId', 'strCreatedTime', 'strName','strModeDelivery','strAdress','action'];
  dataSource =new MatTableDataSource<IorderList>(this.orderList);
  pageRecord=10
  @ViewChild(MatSort, {static: true}) sort: MatSort;
  productLength=this.dataSource.data.length;
  public errors=[]
  ngOnInit(): void {
    this.getPending()
  }

  pageNumberP=0;
  private pendingparam:any={
    "arrStatus":["PENDING"],
    "strDateFilter":null,
    "intLimit":'',
    "strSearch":null,
    "intPageNo":this.pageNumberP
  }
getPending(){
  
  const token = localStorage.getItem('token');
  console.log(token);
  const headers = new HttpHeaders({  'Authorization': token})
  return this.http.post('http://15.207.68.165:4001/order/get_order',this.pendingparam,{headers:headers}).subscribe((pending)=>{
    console.log(pending['arrList']);
    this.orderList=pending['arrList']
    this.dataSource = new MatTableDataSource<IorderList>(this.orderList);
    this.dataSource.sort = this.sort;
    
  })

}
pageNumberU=0;
private unshipparam:any={
  "arrStatus":["UNSHIPPED"],
  "strDateFilter":'',
  "intLimit":'',
  "strSearch":null,
  "intPageNo":this.pageNumberU
}
getshipped(){
 
  
  const headers = new HttpHeaders({  'Authorization': token})
  return this.http.post('http://15.207.68.165:4001/order/get_order',this.unshipparam,{headers:headers}).subscribe((shipped)=>{
    console.log(shipped);
    this.orderList=shipped['arrList']
    this.dataSource = new MatTableDataSource<IorderList>(this.orderList);
    this.dataSource.sort = this.sort;
    
  })

}
pageNumberC=0;
private cancelparam:any={
  "arrStatus":["CANCEL"],
  "strDateFilter":'',
  "intLimit":'',
  "strSearch":null,
  "intPageNo":this.pageNumberC
}
getCancelled(){
 
  
  const headers = new HttpHeaders({  'Authorization': token})
  return this.http.post('http://15.207.68.165:4001/order/get_order',this.cancelparam,{headers:headers}).subscribe((cancel)=>{
    console.log(cancel);
    this.orderList=cancel['arrList']
    this.dataSource = new MatTableDataSource<IorderList>(this.orderList);
    this.dataSource.sort = this.sort;
    
  })

}
pageNumberS=0;
private sendparam:any={
  "arrStatus":["SEND"],
  "strDateFilter":'',
  "intLimit":'',
  "strSearch":null,
  "intPageNo":this.pageNumberS
}
getSend(){
  const headers = new HttpHeaders({  'Authorization': token})
  return this.http.post('http://15.207.68.165:4001/order/get_order',this.sendparam,{headers:headers}).subscribe((send)=>{
    console.log(send);
    this.orderList=send['arrList']
    this.dataSource = new MatTableDataSource<IorderList>(this.orderList);
    this.dataSource.sort = this.sort;
  })
}

valueLimitPending(a){
  console.log(a)
  this.pendingparam.intLimit=a
  this.getPending()
}

valueFilterPending(af){
  console.log(af)
  this.pendingparam.strDateFilter=af
  this.getPending()
}

searchPending(as){
  this.pendingparam.strSearch=as
  this.getPending()
}

valueLimitUnship(b){
  console.log(b)
  this.unshipparam.intLimit=b
  this.getshipped()
}

valueFilterUnship(bf){
  console.log(bf)
  this.unshipparam.strDateFilter=bf
  this.getshipped()
}

searchUnshipped(bs){
  this.unshipparam.strSearch=bs
  this.getshipped()
}
valueLimitCancel(c){
  console.log(c)
  this.cancelparam.intLimit=c
  this.getCancelled()
}

valueFilterCancel(cf){
  this.cancelparam.strDateFilter=cf
  this.getCancelled()
}

searchCancel(cs){
  this.cancelparam.strSearch=cs
  this.getCancelled()
}

valueLimitConfirm(d){
 this.sendparam.intLimit=d
  this.getSend()  
}

valueFilterConfirm(e){
  this.sendparam.strDateFilter=e
  this.getSend()
  console.log(e);
}

searchConfirm(es){
  this.sendparam.strSearch=es
  this.getSend()
}

pageIncC(){
  this.pageNumberC=this.pageNumberC+1;
  this.cancelparam.intPageNo=this.pageNumberC
  this.getCancelled()
  var btn = document.getElementById("btnCounterC");
  if(this.pageNumberC>0){
    btn.removeAttribute('disabled')
  }  
     }
     pageDecC(){
       if(this.pageNumberC)
       this.pageNumberC=this.pageNumberC-1;
       this.cancelparam.intPageNo=this.pageNumberC
       this.getCancelled()
       var btn = document.getElementById("btnCounterC");
     }
     
pageIncS(){
  this.pageNumberS=this.pageNumberS+1;
  this.sendparam.intPageNo=this.pageNumberS
  this.getSend()
  var btn = document.getElementById("btnCounterS");
  if(this.pageNumberS>0){
    btn.removeAttribute('disabled')
  }
     }
     pageDecS(){
       if(this.pageNumberS)
       this.pageNumberS=this.pageNumberS-1;
       this.sendparam.intPageNo=this.pageNumberS
       this.getSend()
       var btn = document.getElementById("btnCounterS");
     }
    
     pageIncU(){
       this.pageNumberU=this.pageNumberU+1;
       this.unshipparam.intPageNo=this.pageNumberU
       this.getSend()
       var btn = document.getElementById("btnCounterU");
       if(this.pageNumberU>0){
         btn.removeAttribute('disabled')
       }
          }
          pageDecU(){
            if(this.pageNumberU)
            this.pageNumberU=this.pageNumberU-1;
            this.unshipparam.intPageNo=this.pageNumberU
            this.getSend()
            var btn = document.getElementById("btnCounterU");
          }
          
          pageIncP(){
            this.pageNumberP=this.pageNumberP+1;
            this.pendingparam.intPageNo=this.pageNumberP
            this.getPending()
            var btn = document.getElementById("btnCounterP");
            if(this.pageNumberP>0){
              btn.removeAttribute('disabled')
            }
               }
               pageDecP(){
                 if(this.pageNumberP)
                 this.pageNumberP=this.pageNumberP-1;
                 this.pendingparam.intPageNo=this.pageNumberP
                 this.getPending()
                 var btn = document.getElementById("btnCounterP");
               }
               openDialogStatus(e) {
                const dialogRef = this.dialog.open(StatusUpdateComponent,{
                    width: '30%',
                  data: {
                    dataKey: e
                  }
                });
                dialogRef.afterClosed().subscribe(result => {
                  console.log(`Dialog result: ${result}`);
                  this.getshipped()
                  this.getCancelled()
                  this.getPending()
                  this.getSend()
                });
              }

              openDialogDelete(p) {
                const dialogRef = this.dialog.open(DeleteOrderComponent,{
                  data: {
                    dataKey: p
                  }
                });
                dialogRef.afterClosed().subscribe(result => {
                  console.log(`Dialog result: ${result}`);
                  this.getshipped()
                  this.getCancelled()
                  this.getPending()
                  this.getSend()
                });
              }
              whatsappMsg(k){
                window.open(k)
              }
}
