import { Component, OnInit } from '@angular/core';
import { ChartOptions, ChartType, ChartDataSets } from 'chart.js';
import { Label } from 'ng2-charts';
import { HttpHeaders, HttpClient } from '@angular/common/http';
@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent implements OnInit {

  detail;
  public bargraph_options;
  public linegraph_options;

  constructor(private http:HttpClient) { }

  ngOnInit(): void {
    let token=localStorage.getItem('token')
    console.log(token)
    this.getDash()
    this.poulateBargraph()
    this.poulateLinegraph()
  }

  poulateBargraph() {

    this.bargraph_options = {
      color: ['#f58634', '#2471A3', '#f4d03f', '#1ABC9C',],
      tooltip: {
        trigger: 'axis',
        axisPointer: {
          type: 'shadow'
        }
      },
      xAxis: [
        {
          type: 'category',
          axisTick: { show: false },
          data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
        }
      ],
      yAxis: [
        {
          type: 'value',
          name: '',
          position: 'left'
        }
      ],
      series: [
        {
          name: 'Sale',
          type: 'bar',
          data: [20,30,40,50,60,70,80],
        }
      ]
    };
  }

  poulateLinegraph() {

    this.linegraph_options = {
      color: ['#f58634', '#2471A3', '#f4d03f', '#1ABC9C',],
      tooltip: {
        trigger: 'axis',
        axisPointer: {
          type: 'shadow'
        }
      },
      xAxis: [
        {
          type: 'category',
          axisTick: { show: false },
          data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
        }
      ],
      yAxis: [
        {
          type: 'value',
          name: '',
          position: 'left'
        }
      ],
      series: [
        {
          name: 'Sale',
          type: 'line',
          smooth: true,
          data: [20,60,40,10,30,50,70],
        }
      ]
    };
  }

  getDash(){
    const token = localStorage.getItem('token');
    const headers = new HttpHeaders({  'Authorization': token})
    return this.http.post('http://15.207.68.165:4001/report/get_dash',null,{headers:headers}).subscribe((data)=>{
      this.detail=data
      console.log( this.detail);
      
    })
  }
  barChartOptions: ChartOptions = {
    responsive: true,
    
  };
  barChartLabels: Label[] = ['Apple', 'Banana', 'Kiwifruit', 'Blueberry', 'Orange', 'Grapes'];
  barChartType: ChartType = 'bar';
  barChartLegend = true;
  barChartPlugins = [];

  barChartData: ChartDataSets[] = [
    { data: [1, 5, 6, 7, 4, 3000000000], label: 'Best Fruits' }
  ];
}
