import { Router } from '@angular/router';
import { FormGroup, FormBuilder } from '@angular/forms';
import { Component, OnInit, Inject } from '@angular/core';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';
import { HttpHeaders, HttpClient } from '@angular/common/http';
const token = localStorage.getItem('token');
@Component({
  selector: 'app-edit-brand',
  templateUrl: './edit-brand.component.html',
  styleUrls: ['./edit-brand.component.scss']
})
export class EditBrandComponent implements OnInit {
  imageChange=false;
  urlBrand:any;
  image;
  editBrand:FormGroup
  constructor(  @Inject(MAT_DIALOG_DATA) public data: any,private fb:FormBuilder,private http:HttpClient,private route:Router) {this.form() }

  ngOnInit(): void {
    // console.log(this.data.result.strImgUrl_0)
    this.urlBrand=this.data.result.strImgUrl_0;
    this.formload()
  
  }
  form(){
    this.editBrand=this.fb.group({
      strName:[''],
      images:['']
    })
  }
  formload(){
    this.editBrand.patchValue({
      strName: this.data.result.strName,
      
    });
    // this.editBrand.get('avatar').updateValueAndValidity()
  }
  onSelectBrand(event) {
    this.imageChange==true;
    const file = (event.target as HTMLInputElement).files[0];
    if (event.target.files && event.target.files[0]) {
      var reader = new FileReader();
  
      reader.readAsDataURL(event.target.files[0]); // read file as data url
  
      reader.onload = (event) => { // called once readAsDataURL is completed
        this.urlBrand = event.target.result;
      }
    }
    this.editBrand.patchValue({
      images: file
    });
    this.editBrand.get('images').updateValueAndValidity()
    

  }
  onSubmit(){
    if(this.imageChange==true){
      let headers = new HttpHeaders({  'Authorization': token, 'strAppInfo': 'TNT1' })
      const formData = new FormData()
      formData.append('images',this.editBrand.get('images').value)
      return this.http.post('http://15.207.68.165:4001/file/files_upload',formData,{headers}).subscribe((body)=>{
        console.log(body)
        this.image=body["arrImageUrl"]
        this.afterSubmit();
       
      
  
      },
      (error)=>{
        if(error){
          console.log(error)
        }
      })
  
    }
    else{
      const headers = new HttpHeaders({ 'Content-Type': 'application/json', 'Authorization': token, 'strAppInfo': 'TNT1' })
      let options={
        "strOperationType":"UPDATE",
        "strDocId":this.data.result._id,
        "strName":this.editBrand.get('strName').value,
        "strCollection":"cln_brand",
        "strImgUrl_0": this.urlBrand
      }
      return this.http.post('http://15.207.68.165:4001/master/create_master',options,{headers}).subscribe((body)=>{
        console.log(body)
        window.location.reload()
      },
      (error)=>{
        if(error){
          console.log(error)
        }
      })

    }

  }
  afterSubmit(){
    const headers = new HttpHeaders({ 'Content-Type': 'application/json', 'Authorization': token, 'strAppInfo': 'TNT1' })
    let options={
      "strOperationType":"UPDATE",
      "strDocId":this.data.result._id,
      "strName":this.editBrand.get('strName').value,
      "strCollection":"cln_brand",
      "strImgUrl_0":this.image
    }
    return this.http.post('http://15.207.68.165:4001/master/create_master',options,{headers}).subscribe((body)=>{
      console.log(body)
      window.location.reload()
    },
    (error)=>{
      if(error){
        console.log(error)
      }
    })
  }
}
