import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Component, OnInit } from '@angular/core';
import { MatSnackBar } from '@angular/material/snack-bar';
import { HttpClient, HttpHeaders } from '@angular/common/http';

@Component({
  selector: 'app-account-settings',
  templateUrl: './account-settings.component.html',
  styleUrls: ['./account-settings.component.scss']
})
export class AccountSettingsComponent implements OnInit {
changeForm:FormGroup
  constructor(private form:FormBuilder,private _snackBar: MatSnackBar,private http:HttpClient) { }
error=false;

  ngOnInit(): void {
    this.fnchangeForm()
  }

fnchangeForm(){
  this.changeForm=this.form.group({
    strpassword:['',Validators.required],
    strpasswordn:['',Validators.required]
  })
}

get f() { return this.changeForm.controls; }

submit(){
  this.error=false;
  const headers = new HttpHeaders({ 'Authorization': localStorage.getItem('token')})
  var params = {
    "strPassword": this.f.strpasswordn.value
               }
  if(this.f.strpassword.value===this.f.strpasswordn.value){
  return this.http.post('http://15.207.68.165:4001/user/change_password',params,{headers:headers}).subscribe((shipped)=>{
    this._snackBar.open('Password changed successfully', '', {
      duration: 2000,
    });
    this.changeForm.reset()
  })}
  else{
    this.error=true;
  }
}

}
