import { Component, OnInit, Inject } from '@angular/core';
import { HttpHeaders, HttpClient } from '@angular/common/http';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';

@Component({
  selector: 'app-delete-order',
  templateUrl: './delete-order.component.html',
  styleUrls: ['./delete-order.component.scss']
})
export class DeleteOrderComponent implements OnInit {

  constructor( @Inject(MAT_DIALOG_DATA) public data: any,private http:HttpClient,private _snackBar: MatSnackBar) { }

  ngOnInit(): void {
    console.log(this.data.dataKey);
    
  }
  delete(){
    const token = localStorage.getItem('token');
  const headers = new HttpHeaders({  'Authorization': token})
    let param={"strOrderId":this.data.dataKey,
    "strOrderStatus":"DELETE"
    }
    return this.http.post('http://15.207.68.165:4001/order/update_order',param,{headers:headers}).subscribe((body)=>{
    console.log(body);
    this._snackBar.open('You have  successfully deleted', '', {
      duration: 2000,
    });
    })
  }
}
