import { OtpChangePasswordComponent } from './../dialog/otp-change-password/otp-change-password.component';
import { AuthGuard } from './../_helpers/auth.guard';
import { AuthenticationService } from './../_services/authentication.service';
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { Router, ActivatedRoute } from '@angular/router';
import { first } from 'rxjs/operators';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {

  loginForm: FormGroup;
  loading=false;
  hide = true;
  UserNotFound:boolean=false;
  submitted = false;
  returnUrl: string;
  loginCredential:any;
  error:boolean=false;
  
  constructor(private fb: FormBuilder,
    public dialog: MatDialog,
    private router:Router,
    private AuthenticationService:AuthenticationService,
    private AuthGuard:AuthGuard,

    private route: ActivatedRoute,)
   { 
  
    }

  ngOnInit(): void {
    const loggedIn =JSON.parse( localStorage.getItem('loggedIn'))
    
    if(loggedIn){
     let token= localStorage.setItem('token',loggedIn.strEncryptToken);
    
      this.router.navigate(['/home/dashboard'] );
    
     }
    this.loginForm = this.fb.group({
      username: ['', Validators.required],
      password: ['', Validators.required]
  });
    
 
  }
  get f() { return this.loginForm.controls; }
 
  onSubmit() {
    this.submitted = true;

    // stop here if form is invalid
    // if (this.loginForm.invalid) {
    //     return;
    // }

    this.loading = true;
    this.AuthenticationService.login(this.f.username.value, this.f.password.value)
        .pipe(first())
        .subscribe((body)=>{
          console.log(body)
          
          if(body['blnAPIStatus']==true){
            localStorage.setItem('token',body['strEncryptToken'])
            localStorage.setItem('id',body['_id'])
            this.router.navigate(['/home/dashboard'] );
           console.log(localStorage.getItem('token'));
           
           
        }
      
        },
        (error)=>{
          if(error){
            this.error=true
        
          
          }
        })
        
}
openDialogPassword() {
  const dialogRef = this.dialog.open(OtpChangePasswordComponent,{
  
  });

  dialogRef.afterClosed().subscribe(result => {
    console.log(`Dialog result: ${result}`);
  });
}


}
