import { environment } from './../../environments/environment.prod';
import { Component, OnInit } from '@angular/core';
import { HttpHeaders, HttpClient } from '@angular/common/http';
import { FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Observable } from 'rxjs';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router, ActivatedRoute } from '@angular/router';

const token = localStorage.getItem('token');


const headers = new HttpHeaders({ 'Content-Type': 'application/json', 'strAppInfo': 'big' })

const header = new HttpHeaders({  'Authorization': token })
const head = new HttpHeaders({ 'Content-Type': 'application/json', 'Authorization': token ,'strAppInfo': 'big' })
export interface User {
  name: string;
}
@Component({
  selector: 'app-edit-product',
  templateUrl: './edit-product.component.html',
  styleUrls: ['./edit-product.component.scss']
})
export class EditProductComponent implements OnInit {

  private master: any = {
    "strCollection": "cln_brand",
    "strValue": "",
  }
  private cat: any = {
    "strCollection": "cln_category",
    "strValue": "",
  }
  myControl = new FormControl();
  productForm:FormGroup;
  varForm:FormGroup;
  urls=[];
  urlCategory:any
  urlBrand:any;
  brand=[];
  category=[];
  image=[];
  file: any;
  url=[];
  private files=[];
  // brandAuto=[];
  categoryAuto=[];
  filteredOptions: Observable<string[]>;
  stateCtrl: FormControl;
  options: any[];
  subscription: any;
  catAuto:string[];
  brandAuto: string[];
  filteredOptions1: Observable<string[]>;
  urlBrandFile;
  newBrandImage;
  urlCategoryFile;
  newCategoryImage;
  id:any;
  productList: any;
  varAuto;
  varList=[];
  arraySpec=[];

  constructor(private fb:FormBuilder,private http:HttpClient,private _snackBar: MatSnackBar,private route:Router,private router:ActivatedRoute) {this.addProduct(); }

  ngOnInit(): void {
    this.id=this.router.snapshot.params['id']
    console.log(this.id)
    this.autoCompleteBrand()
    this.loadBrandCategory();
    this.autocompletecat();
    this.listProduct()
  }

  addVariance(){
    this.varList.push(
      {"strName" :this.vf.varName.value  ,
      "dblMRP": this.vf.varMrp.value,
      "dblSellingPrice":this.vf.varPrice.value}
    )
  this.varForm.reset()
    this._snackBar.open('Variance added', '', {
      duration: 2000,
    });
  }
  
  deleteVariance(row){
    this.varList = this.varList.filter(item => item.strName !== row.strName)
  }
  
  editVariance(i, row){
    this.varForm.patchValue({
      varName:this.varList[i].strName,
        varMrp:this.varList[i].dblMRP,
        varPrice:this.varList[i].dblSellingPrice
    })
    this.varList = this.varList.filter(item => item.strName !== row.strName)
  }

listProduct(){
  let param={"intLimit":1,
  "intPageNo":0,
      "strSort":"strName",
      "strSortActive":"ASC",
      "arrCategory":[],
      "arrBrands":[],
      "arrProductName":[this.id]
  }
  return this.http.post(environment.apiUrl+'product/get_product',param,{headers:head}).subscribe((body)=>{
    this.productList=body['arrList']
    console.log(this.productList);
    
   this.urls=this.productList[0].arrImageUrl,
   this.urls.forEach(image=>{
     this.image.push(image)
   })
  //  this.image.push(this.productList[0].arrImageUrl)
  //  this.productForm.patchValue({
  //   images: this.files
  // });
  this.productForm.get('images').updateValueAndValidity()
  console.log(this.productList[0].arrSpecification)
  // if(this.productList.arrSpecification.length){
  //   spec=this.productList.arrSpecification.add(",")
  //       }
    this.productForm.patchValue({
      strName:this.productList[0].strName,
      strDescription:this.productList[0].strDescription,
      arrSpecification:this.productList[0].arrSpecification,
      strSKUId:this.productList[0].strSKUId,
      strCategory:this.productList[0].strCategory,
      strBrand:this.productList[0].strBrand,
      dblMRP:this.productList[0].dblMRP,
      strStock:this.productList[0].strStock,
      position:this.productList[0].intPosition,
      status:this.productList[0].chrStatus,
      dblSellingPrice:this.productList[0].dblSellingPrice,
      intPosition:this.productList[0].intPosition,
      images:this.productList[0].arrImageUrl,
      strNewbrand:[''],
      strNewCategory:['']
    });
    this.varList=this.productList[0].arrVariance
  })
}

  addProduct(){
    this.productForm=this.fb.group({
      strName:[''],
      strSKUId:[''],
      strCategory:[''],
      strBrand:[''],
      dblMRP:[''],
      strStock:[''],
      position:[''],
      status:[''],
      dblSellingPrice:[''],
      intPosition:[''],
      images:[''],
      strNewbrand:[''],
      strNewCategory:[''],
      strVarName:[''],
      strVarPrice:[''],
      strVar:[''],
      arrSpecification:[''],
      strDescription:[''],

    })
    this.varForm=this.fb.group({
      varName:[''],
      varMrp:[''],
      varPrice:['']
    })
  }
  get f() { return this.productForm.controls; }
  get vf() { return this.varForm.controls; }
 
  onSelectFile(event) { // called each time file input changes
    //const file = (event.target as HTMLInputElement).files[0];
    if (event.target.files && event.target.files[0]) {
      var filesAmount = event.target.files.length;
      console.log(filesAmount)
      const f=event.target.files;
      console.log(f[0])
      for(let i=0; i<filesAmount;i++){
        this.files.push(f[i])

      }
      console.log(this.files)
      //this.files=(event.target as HTMLInputElement).files;
      
      for (let i = 0; i < filesAmount; i++) {
              var reader = new FileReader();
              // reader.onload = (event) => { // called once readAsDataURL is completed
              //   this.urls = event.target.result;
              // }

              reader.onload = (event:any) => {
                console.log(event.target.result);
                 this.urls.push(event.target.result); 
                //  this.files.push(event.target.files[i]);

                
              }

              reader.readAsDataURL(event.target.files[i]);
         
      }
     
    
   
    
      
  }
  this.productForm.patchValue({
    images: this.files
  });
  this.productForm.get('images').updateValueAndValidity()
}

removeSelectedFile(index){
  this.urls.splice(index,1);
  this.image.splice(index,1)
}
onSelectBrand(event) {
  this.urlBrandFile=(event.target as HTMLInputElement).files[0];
  if (event.target.files && event.target.files[0]) {
    var reader = new FileReader();

    reader.readAsDataURL(event.target.files[0]); // read file as data url

    reader.onload = (event) => { // called once readAsDataURL is completed
      this.urlBrand = event.target.result;
     
    }
    
  }
}
onSelectCategory(event) {
  this.urlCategoryFile=(event.target as HTMLInputElement).files[0];
  if (event.target.files && event.target.files[0]) {
    var reader = new FileReader();

    reader.readAsDataURL(event.target.files[0]); // read file as data url

    reader.onload = (event) => { // called once readAsDataURL is completed
      this.urlCategory = event.target.result;
    }
  }
}
loadBrandCategory(){
  let options={
    "arrCollection":[
     {"strCollection":"cln_brand"},
     {"strCollection":"cln_category"}
    ]

  }
  return this.http.post(environment.apiUrl+'master/get_master',options,{headers}).subscribe((body)=>{
    this.brand=body['cln_brand'];
    this.category=body['cln_category'];
    // console.log(this.brand);
    // console.log(this.category)
  })
}
uploadImage(){
  // console.log("value")
  // console.log(this.productForm.get('images').value)
  // console.log("length")
  // console.log(this.productForm.get('images').value.length)
  console.log(this.files)
  const formData = new FormData();
  this.files.forEach((file) => formData.append('images', file));
 
  return this.http.post('http://15.207.68.165:4001/file/files_upload',formData,{headers:header}).subscribe((body)=>{
    console.log(body)
    body["arrImageUrl"].forEach(element => {
      this.image.push(element)
      
    });
    // this.image.push(body["arrImageUrl"])
    this._snackBar.open('You have  successfully uploaded', 'CLOSE', {
      duration: 2000,
    });
    
  

  },
  (error)=>{
    if(error){
      console.log(error)
    }
  })


}
onSubmit(){
  let params={
    "strOperationType":'UPDATE',
    "strDocId":this.productList[0]._id,
    "strName":this.f.strName.value,
    "strSKUId":this.f.strSKUId.value,
    "dblMRP":this.f.dblMRP.value,
    "dblSellingPrice":this.f.dblSellingPrice.value,
    "strStock":this.f.strStock.value,
    "intPosition":this.f.intPosition.value,
    "arrImageUrl":this.image,
    "strBrand":this.f.strBrand.value,
    "strCategory":this.f.strCategory.value,
    "arrVariance":this.varList,
    "arrSpecification":this.arraySpec,
    "strDescription":this.f.strDescription.value,
  }
 
console.log(params);

  this.http.post('http://15.207.68.165:4001/product/create_product',params,{headers:head}).subscribe((body)=>{
    this._snackBar.open('You have  successfully added', 'CLOSE', {
      duration: 2000,
    });
    this.route.navigateByUrl('/home/product')
  })
}
valueChanged(e) {
  this.master.strValue = e
  console.log(this.master)
}
autoCompleteBrand(){

  return this.http.post(environment.apiUrl+'common/get_autocomplete',this.master,{headers}).subscribe((body)=>{
    this.brandAuto=body['arrList']
    
  
  })
  
}
valueChangedcat(e) {
  this.cat.strValue = e
  console.log(this.cat)
}
autocompletecat(){

  return this.http.post(environment.apiUrl+'common/get_autocomplete',this.cat,{headers}).subscribe((body)=>{
    this.catAuto=body['arrList']
    
  
  })
  
}



  

autoCompleteCategory(){
  let param={
    
    "strCollection":"cln_category",
    "strValue":"OP",
   
  
}
return this.http.post(environment.apiUrl+'common/get_autocomplete',param,{headers}).subscribe((body)=>{
  this.categoryAuto=body['arrList']
})

}
addBrandImage(){
  let headers = new HttpHeaders({  'Authorization': token })
  const formData = new FormData()
  formData.append('images',this.urlBrandFile)
  return this.http.post('http://15.207.68.165:4001/file/files_upload',formData,{headers}).subscribe((body)=>{
    console.log(body)
    this.newBrandImage=body["arrImageUrl"]
    this.addBrand();
    
  

  },
  (error)=>{
    if(error){
      console.log(error)
    }
  })
}
addBrand(){
  let headers = new HttpHeaders({  'Authorization': token })
  console.log(this.productForm.get('strNewbrand').value)
  let param={
       
    strCollection:"cln_brand",
    strName:this.productForm.get('strNewbrand').value,
    strImgUrl_0:this.newBrandImage

  }
  return this.http.post('http://15.207.68.165:4001/master/create_master',param,{headers}).subscribe((body)=>{
    console.log(body)
    this._snackBar.open('You have  successfully added', 'CLOSE', {
      duration: 2000,
    });
  },
  (error)=>{
    if(error){
      console.log(error)
    }
  })
}
addCategoryImage(){
  let headers = new HttpHeaders({  'Authorization': token })
  const formData = new FormData()
  formData.append('images',this.urlCategoryFile)
  return this.http.post('http://15.207.68.165:4001/file/files_upload',formData,{headers}).subscribe((body)=>{
    console.log(body)
    this.newCategoryImage=body["arrImageUrl"]
    this.addCategory();
    
  

  },
  (error)=>{
    if(error){
      console.log(error)
    }
  })
}
addCategory(){
  let headers = new HttpHeaders({  'Authorization': token })
  let param={
       
    strCollection:"cln_category",
    strName:this.productForm.get('strNewCategory').value,
    strImgUrl_0:this.newCategoryImage

  }
  return this.http.post('http://15.207.68.165:4001/master/create_master',param,{headers}).subscribe((body)=>{
    console.log(body)
    this._snackBar.open('You have  successfully added', 'CLOSE', {
      duration: 2000,
      panelClass: ['simple-snack-bar']
    });
  },
  (error)=>{
    if(error){
      console.log(error)
    }
  })
}

}
