import { Component, OnInit, Inject } from '@angular/core';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { HttpHeaders, HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-delete-customer',
  templateUrl: './delete-customer.component.html',
  styleUrls: ['./delete-customer.component.scss']
})
export class DeleteCustomerComponent implements OnInit {

  constructor( @Inject(MAT_DIALOG_DATA) public data: any,private http:HttpClient,private _snackBar: MatSnackBar) { }

  ngOnInit(): void {
    console.log(this.data.dataKey)
  }

  delete(){
    const token = localStorage.getItem('token');
    const header = new HttpHeaders({  'Authorization': token })
    let param={"arrDeleteId":[this.data.dataKey]}
    return this.http.post('http://15.207.68.165:4001/user/delete_user',param,{headers:header}).subscribe ( (list)=>{
      this._snackBar.open('Customer deleted successfully', '', {
        duration: 2000,
      })
    window.location.reload()
    }) 
  }
}