import { HttpClient, HttpHeaders } from '@angular/common/http';
import { FormGroup, FormBuilder } from '@angular/forms';
import { Component, OnInit, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialog } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';

@Component({
  selector: 'app-status-update',
  templateUrl: './status-update.component.html',
  styleUrls: ['./status-update.component.scss']
})
export class StatusUpdateComponent implements OnInit {
statusForm:FormGroup
  constructor(private form:FormBuilder, @Inject(MAT_DIALOG_DATA) public data: any,private http:HttpClient,private _snackBar: MatSnackBar) {}

  ngOnInit(): void {
    this.status()
    console.log(this.data.dataKey);
    
  }
  status(){
    this.statusForm=this.form.group({
      strStatus:['']
    })
  }
  get f() { return this.statusForm.controls; }
  statusUpdate(){
    const token = localStorage.getItem('token');
  console.log(token);
  const headers = new HttpHeaders({  'Authorization': token})
    let param={"strOrderId":this.data.dataKey,
    "strOrderStatus":this.f.strStatus.value
    }
    
    
    return this.http.post('http://15.207.68.165:4001/order/update_order',param,{headers:headers}).subscribe((body)=>{
    console.log(body);
    this._snackBar.open('You have  successfully changed status', '', {
      duration: 2000,
    });
    window.location.reload()
    })
  }

 
}
